.section-portofolio {
    height: 100%;
    padding-bottom: 16rem;
    margin-top: -5rem;
    margin-bottom: -5rem;
    background-color: $color-primary-light;

    &__container {
    }

    &__heading-box {
        color: $color-secondary-dark;
        margin-bottom: 6rem;
        padding-top: 8rem;
        margin-bottom: 1rem;
    }

    &__heading {
        text-align: center;
        font-size: 4rem;
        font-weight: 400;
        letter-spacing: .1rem;
    }

    &__button-box-2 {
        text-align: center;
        padding-top: 10rem;
        border-top: solid #2020204d;

        &__button {
            display: inline-block;
            padding: 0 1rem 0 1rem;
            font-size: 7rem;
            font-weight: 900;
            text-transform: uppercase;
        }
    }
}