.about {
    height: 100vh;
    z-index: 1;
    background-color: $color-primary-light;
    position: relative;

    &__left-side {
        width: 17.5%;
        height: 100vh;
        position: fixed;
        top: 0;
        bottom: 0;

        

        
        &__background-image {
            background-image: linear-gradient(
            to left, 
            rgba($color-secondary-light, 0), 
            rgba($color-secondary-dark, 0) ) , 
            url(../../img/about-2.jpg);
            background-size: cover;
            overflow: hidden;

            animation: moveInAboutImg .5s ease-out;
        }
        
    }


    &__right-side {
        width: 82.5%;
        background-color: $color-primary-light;
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        overflow-y: auto;

        &__title {
            font-size: 10rem;
            font-weight: 900;
            background-color: $color-secondary;
            color: $color-primary-light;
            text-transform: uppercase;
            padding: 1rem 6rem 1rem 6rem;
            letter-spacing: 1rem;

        }

        &__box-1 {
            background-color: $color-primary-light;
            padding-bottom: 1.5rem;
            border-bottom: solid #1616162a;
        }

        &__heading-1 {
            font-size: 5rem;
            font-weight: 400;
            color: $color-secondary;
            margin: 1.2rem 6rem 0rem 6rem;
            letter-spacing: .3rem;
        }
        
        &__p-1 {
            font-size: 2rem;
            font-weight: 400;
            color: $color-secondary;
            margin: 0rem 6rem -1.1rem 6rem;
            padding-bottom: 2.2rem;
        }

        &__phone {
            font-size: 2rem;
            font-weight: 700;
            color: $color-secondary;
            margin: 0rem 6rem -1.1rem 6rem;
            padding-bottom: 2.2rem;
        }

        &__mail {
            font-size: 2rem;
            font-weight: 700;
            color: $color-secondary;
            margin: 0rem 6rem 1rem 6rem;
            padding-bottom: 2.2rem;
        }

        &__contact {

        }

        &__social-box {
            margin: 0rem 6rem 0rem 6rem;
            &:hover {
                transform: scale(1);;
            }
        }

        &__resume {
            margin: 0rem 6rem 0rem 6rem;
            padding-bottom: 4rem;
            border-bottom: solid #161616b0;
        }

        &__button {
            margin: 7rem 6rem 7rem 12rem;
        }
    }
}